@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap");

.card_container {
    opacity: 0.9;
    border-radius: 5px;
    background: rgb(24, 81, 149);
    background: linear-gradient(90deg,
            rgba(24, 81, 149, 1) 0%,
            rgba(15, 33, 54, 1) 100%);
    margin: 20px 0 0 0;
    box-shadow: black 3px 0 15px 1px;
    display: flex;
    justify-content: space-between;
    max-width: 100%;
    height: auto;
    font-family: "Josefin Sans", sans-serif;
    color: aquamarine;
}

